<template>
  <div class="page">
    <simple-header title="知乎登录"></simple-header>
    <page-content>
      <form-list class="login-padding">
        <div slot="title">知乎</div>
        <form-item>
          <div class="item-content">
            <div class="item-input">
              <input v-model="user.username" type="text" placeholder="手机号">
            </div>
          </div>
        </form-item>
        <form-item>
          <!--<div class="item-media">
            <img src="../assets/images/form/i-form-name.png" alt="" width="30" />
          </div>-->
          <div class="item-content">
            <!--<div class="item-title label">E-mail</div>-->
            <div class="item-input">
              <input v-model="user.password" type="text" placeholder="验证码">
            </div>
            <div class="item-after">
              <m-button @click.native="getUserInfo">用户信息</m-button>
            </div>
          </div>
        </form-item>
        <form-item>
          <!--<div class="item-media">
            <img src="../assets/images/form/i-form-name.png" alt="" width="30" />
          </div>-->
          <div class="item-content">
            <!--<div class="item-title label">E-mail</div>-->
            <div class="item-input">
              <input  type="text" v-model="user.vcode" placeholder="验证码">
            </div>
            <div class="item-after" @click="changeVcode()">
              <img class="weui_vcode weui_cell_ft" :src="img_url | base64">
            </div>
          </div>
        </form-item>
        <div class="content-block content-padded">
          <div class="row">
            <!--<div class="col-50"><m-button type="danger" size="large">Cancel</m-button></div>-->
            <div class="col-100"><m-button size="large" @click.native="login()">登录</m-button></div>
          </div>
        </div>
        <!--<div slot="append">The usage of Form is just the same as List.</div>-->
      </form-list>
    </page-content>

    <toast :text="searchInput" ref="msg"></toast>
  </div>
</template>
<style>
  .login-padding{
    padding-top: 10%;
  }
</style>
<style>
  @import url("../lib/weui.css");
</style>
<script>
import { SimpleHeader } from '../components/header'
import Content from '../components/content'
import Searchbar from '../components/searchbar'
import Toast from '../components/toast'
import { Button } from '../components/buttons'
import { Form, FormItem } from '../components/form'
import Switcher from '../components/switcher'
import {setCookie,getCookie} from '../lib/cookie'

export default {
  components: {
    SimpleHeader,
    'page-content': Content,
    Searchbar,
    Toast,
    'm-button': Button,
    Switcher,
    'form-list': Form,
    FormItem
  },
  data () {
    return {
      searchInput: '',
      user:{
        username:'17801002476',
        password:'c846008154601358',
        vcode:''
      },
      img_url:''
    }
  },
  filters:{
    base64(param){
      return 'data:image/png;base64,'+param;
    }
  },
  beforeMount(){
    //this.getAlbumList();
    //setCookie('tooken','tokenStirng123456ajsodi',1000*60*60);
    //this.changeVcode();
  },
  methods: {
    login (){
      this.$http.post('/user/userLogin',this.user).then((result)=>{
        let data = result.data;
        let code = data.code;
        if(code==0){
          alert(data.msg);
          setCookie('token',data.data,7);
        }else{
          alert(data.msg);
        }
      })
    },
    getUserInfo(){
      this.$http.get('/user/userInfo').then((result)=>{
        alert(result.data);
        console.log(result);
      });
    },
    changeVcode(){
      this.$http.get('/user/getVcode').then((result)=>{
        let msg=result.data;
        this.img_url=msg.data.vcode;
      })
    }
  }
}
</script>



